import React from "react";
import { Form, Card, Input, DatePicker, Button } from "antd";

const { RangePicker } = DatePicker;
const { TextArea } = Input;

const BasicFormPage = (props) => {
  const { submitting } = props;
  const [form] = Form.useForm();
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 7 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
      md: { span: 10 },
    },
  };
  const submitFormLayout = {
    wrapperCol: {
      xs: { span: 24, offset: 0 },
      sm: { span: 10, offset: 7 },
    },
  };
  const onFinish = (values) => {
    console.log("Success:", values);
  };
  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  return (
    <div>
      <Card>
        <Form form={form} onFinish={onFinish} onFinishFailed={onFinishFailed}>
          <Form.Item {...formItemLayout} label="" name="field1" rules={[{ required: true, message: "" }]}>
            <Input placeholder="" />
          </Form.Item>
          <Form.Item {...formItemLayout} label="" name="field2" rules={[{ required: true, message: "" }]}>
            <RangePicker style={{ width: "100%" }} placeholder={["", ""]} />
          </Form.Item>
          <Form.Item {...formItemLayout} label="" name="field4" rules={[{ required: true, message: "" }]}>
            <TextArea style={{ minHeight: 32 }} placeholder="" rows={4} />
          </Form.Item>
          <Form.Item {...formItemLayout} label="" name="field5" rules={[{ required: true, message: "" }]}>
            <TextArea style={{ minHeight: 32 }} placeholder="" rows={4} />
          </Form.Item>
          <Form.Item {...formItemLayout} label="" name="field6">
            <Input placeholder="" />
          </Form.Item>
          <Form.Item {...submitFormLayout} style={{ marginTop: 32 }}>
            <Button type="primary" htmlType="submit" loading={submitting}>
              
            </Button>
            <Button style={{ marginLeft: 8 }}></Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  );
};

export default BasicFormPage;
